.container {
    height: 100%;
    display: flex;
    justify-content: center;
    width: 1000px;
    margin: 0 auto;
}

.movies {
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 90%;
    padding-top: 70px;
}